.verticalLine {
  position: absolute;
  border: none;
  width: 1px;
  background-color: black;
}

.horizontalLine {
  border: none;
  position: absolute;
  height: 1px;
  background-color: black;
}

.liquid {
  position: absolute;
  border: none;
  background-color: DarkSlateBlue;
}

#runButton {
  top: 10px;
  left: 50px;
}

.slider {
  left: 10px;
}

#sliderWrapper{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

#myChart{
  position: absolute;
  top: 75%;
  width: 50px;
}

.score {
  position: absolute;
  transform: translateX(-50%);
  transform: translateY(-50%);
}
